import React, {Component} from 'react';
import {Card,CardImg,CardImgOverlay, CardText,CardBody,CardTitle,Breadcrumb, BreadcrumbItem} from 'reactstrap';
import {Link } from 'react-router-dom';
import COMMENTS from '../shared/comments';
class ShoeDetail extends Component{
    constructor(props){
        super(props);

        this.state={
            selectedComments : COMMENTS
        };
    }
    renderComments(CommentArray){
        
        if (CommentArray !=null){
        const menu = CommentArray.map((dish)=>{ 
            return(
            <div className='container'>
            <ul className='list-unstyled'>
                    <p><b>{dish.comment}</b></p> 
                    <p> -- {dish.author} ,  {dish.date}</p>
            </ul>
            </div>
        );
        
    });
    return menu;
}
else{
    return (<div></div>);
}
}
    render(){
        const select = this.props.dish;
        
        if(select !=null){
            let SelecteddishComments = this.state.selectedComments.filter(  (dish) => {
                return dish.dishId  === select.id});

                const render = this.renderComments(SelecteddishComments)
            return(
                <div className='container' >
                <div className='row'>
                    <Breadcrumb>
                        <BreadcrumbItem>
                        <Link to="/home">Home</Link>
                        </BreadcrumbItem>
                        <BreadcrumbItem >
                        <Link to="/featureditems">Shope Now</Link>
                        </BreadcrumbItem>
                        <BreadcrumbItem active>{select.name}</BreadcrumbItem>
                    </Breadcrumb>
                <div className="col-sm-12 col-md-5 m-1">
                <Card>
                    <CardImg  object src={select.image} alt={select.name} />
                    <CardBody>
                    <CardTitle heading>{select.name}</CardTitle>
                    <CardText>{select.description}</CardText>
                    </CardBody>
                </Card>
                </div>
                <div className="col-sm-12 col-md-5 m-1">
                <h4>Comments</h4>
                {render}
                </div>
                </div>
                </div>
            );
        }
        else{
            return(<div></div>);
        }
    
    }

}
export default ShoeDetail;