import React,{Component} from 'react';
import { Navbar, NavbarBrand,Nav,NavbarToggler,NavItem,Collapse, Jumbotron,Button ,Modal, ModalHeader,ModalBody, 
    Input, FormGroup, Label, Form } from 'reactstrap';
import {NavLink} from 'react-router-dom';
class Header extends Component{
    constructor(props){
        super(props);
        this.state={
            isNavOpen: false,
            isModalOpen : false
        };
        this.toggleNav = this.toggleNav.bind(this);
        this.toggleModal = this.toggleModal.bind(this);
        this.handleLogin = this.handleLogin.bind(this);

    }

    toggleNav(){
        this.setState({
            isNavOpen : !this.state.isNavOpen
        });
    }
    
    toggleModal(){
        this.setState({
            isModalOpen : !this.state.isModalOpen
        });

    }
    handleLogin(event){
     this.toggleModal();
     alert("Username: "+ this.username.value + ",Password: "+ this.password.value + ",Remember me: "+ this.remember.checked)

    }
    render() {

        return(
            <div>
                <React.Fragment>
                    <Navbar dark expand="md" className="bg-dark">
                        <div className ="container">
                            <NavbarToggler onClick={this.toggleNav} />
                            <NavbarBrand className="mr-auto" href="https://bilalquresi.github.io/ecommerce-website">
                            <img src="assets/images/logo.jpg" height = "50" width="50"
                            alt="Flex Toe"/>
                            </NavbarBrand>
                            <Collapse isOpen={this.state.isNavOpen} navbar>
                            <Nav navbar>
                                <NavItem>
                                    <NavLink className="nav-link" to="/home"><span className="fa fa-home fa-lg"></span> Home </NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="nav-link" to="/featureditems"><span className="fa fa-shopping-cart fa-lg"></span> Shop Now </NavLink>
                                </NavItem>
                                {/* <NavItem>
                                    <NavLink className="nav-link" to="/aboutus"><span className="fa fa-sign-in fa-lg"></span> Signup/Login </NavLink>
                                </NavItem> */}
                                <NavItem>
                                    <NavLink className="nav-link" to="/contactus"><span className="fa fa-address-card fa-lg"></span> Contact Us </NavLink>
                                </NavItem>
                            </Nav>
                            <Nav className="ms-auto" navbar>
                                <NavItem>
                                    <Button outline onClick={this.toggleModal}>
                                        <span className="fa fa-sign-in fa-lg"></span> Login
                                    </Button>
                                </NavItem>

                            </Nav>
                            </Collapse>
                        </div>
                    </Navbar>
                    <Jumbotron>
                        <div className="container">
                            <div className="row row-header">
                                <div className="col-12 col-sm-6">
                                    <h1>Flex Toe</h1>
                                    <p1>Foot wear with Style</p1>
                                </div>
                            </div>
                        </div>
                    </Jumbotron>
                    <Modal isOpen={this.state.isModalOpen} toggle = {this.toggleModal}> 
                            <ModalHeader toggle = {this.toggleModal}>Login </ModalHeader>
                            <ModalBody>
                                <Form onSubmit = {this.handleLogin}>
                                    <FormGroup>
                                        <Label htmlFor = "username">Username</Label>
                                        <Input type = "text" id="username" name="username"
                                        innerRef={(input) => this.username = input} />
                                    </FormGroup>
                                    <FormGroup>
                                        <Label htmlFor = "password">Password</Label>
                                        <Input type = "password" id="password" name="password"
                                         innerRef={(input) => this.password = input} />
                                    </FormGroup>
                                    <FormGroup check>
                                        <Label check>
                                            <Input type="checkbox" name="remember"
                                             innerRef={(input) => this.remember = input} />
                                            Remember Me
                                        </Label>
                                    </FormGroup>
                                    <Button type="submit" value="submit" color="primary">Login</Button>
                                </Form>
                            </ModalBody>
                    </Modal>
                </React.Fragment>
            </div>
        );
    }
}

export default Header;