import React,{Component} from 'react';
import {Form , Button, FormGroup, Label, Input, Col, FormFeedback} from 'reactstrap';

class Contact extends Component{
    constructor(props){
        super(props);
    this.state={
        firstname: '',
        lastname: '',
        telnum: '',
        email: '',
        agree: false,
        contactType: 'Tel.',
        message:'',
        touched:{
            firstname:false,
            lastname:false,
            telnum:false,
            email:false
        }

    }
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleBlur = this.handleBlur.bind(this);

}
    handleInputChange(event){
        const target = event.target;
        const value = target.type === "checkbox" ? target.checked : target.value;
        const name = target.name;
        console.log(event.target);

        this.setState({
            [name] : value
        });

    }
    handleBlur = (field) =>(evt) =>{
        this.setState({
            touched: { ... this.state.touched,[field]:true}
        });
    }
    handleSubmit(event){
     console.log("Current state is: " + JSON.stringify(this.state));
     alert("Current state is: "+JSON.stringify(this.state)); 
     event.preventDefault();  
    }
    validate(firstname,lastname,telnum,email){
        const errors ={
            firstname:'',
            lastname : '',
            telnum : '',
            email : ''
        };
        if(this.state.touched.firstname && firstname.length < 3){
            errors.firstname = "First name should be >= 3";
        }
        const reg = /^\d+$/;
        if(this.state.touched.telnum && !(reg.test(telnum))){
            errors.telnum = "Tel No. should contain only numbers";
        }
        if(this.state.touched.email && email.split('').filter(x => x === '@').length!=1){
            errors.email = "Not a valid email";
        }
        return errors;
    }

render(){
    const errors = this.validate(this.state.firstname,this.state.lastname,this.state.telnum,this.state.email);
return(
    <div className="container">
        <div className="row row-content">
            <div className="col-12">
                <h3>Location Information</h3>
            </div>
           <div className ="col-12 col-sm-4 offset-sm-1">
               <h5>Address</h5>
                    <address id="contactaddress">
		              Gulshan-e-Shamim<br />
		              Federal B Area, Karachi<br />
		              Pakistan<br />
		              <i className="fa fa-phone fa-lg"></i> :<a href="tel:+923000652715"> +923000652715 </a><br />
		              <i className="fa fa-envelope fa-lg"></i> : <a href="mailto:fasihimran41@gmail.com">
                         fasihimran41@gmail.com</a>
                    </address>
           </div>
           <div className="col-12 col-sm-6 offset-sm-1">
                    <h5>Map of our Location</h5>
                </div>
                <div className="col-12 col-sm-11 offset-sm-1">
                    <div className="btn-group" role="group">
                        <a role="button" className="btn btn-primary" href="tel:+923000652715"><i className="fa fa-phone"></i> Call</a>
                        <a role="button" className="btn btn-info" href="https://www.instagram.com/flextoeoffical/"><i className="fa fa-instagram"></i> Instagram</a>
                        <a role="button" className="btn btn-success" href="mailto:fasihimran41@gmail.com"><i className="fa fa-envelope-o"></i> Email</a>
                    </div>
            </div>
        </div>
        <div className='row row-content'>
            <div className='col-12'>
                <h3>Send us your feedback</h3>
            </div>
            <div className='col-12 col-md-9'>
                <Form onSubmit={this.handleSubmit}> 
                    <FormGroup row>
                        <Label htmlFor='firstname' md={2}>First Name</Label>
                        <Col md={10}>
                            <Input type="text" id="firstname" name="firstname" placeholder="firstname"
                             value={this.state.firstname}
                             onBlur = {this.handleBlur('firstname')}
                             valid = {errors.firstname === ''}
                             invalid = {errors.firstname != ''}
                             onChange = {this.handleInputChange} />
                            <FormFeedback>{errors.firstname}</FormFeedback>
                        </Col>
                    </FormGroup>
                    
                    <br></br>
                    <FormGroup row>
                        <Label htmlFor='lastname' md={2}>Last Name</Label>
                        <Col md={10}>
                            <Input type="text" id="lastname" name="lastname" placeholder="lastname"
                             value={this.state.lastname}
                             onBlur = {this.handleBlur('lastname')}
                             valid = {errors.lastname === ''}
                             invalid = {errors.lastname != ''}
                             onChange = {this.handleInputChange}  />
                            <FormFeedback>{errors.lastname}</FormFeedback>
                        </Col>
                    </FormGroup>

                    <br></br>
                    <FormGroup row>
                        <Label htmlFor='telnum' md={2}>Contact No.</Label>
                        <Col md={10}>
                            <Input type="tel" id="telnum" name="telnum" placeholder="Tel. Number"
                             value={this.state.telnum}
                             valid = {errors.telnum === ''}
                             invalid = {errors.telnum != ''}
                             onBlur = {this.handleBlur('telnum')}
                             onChange = {this.handleInputChange}  />
                            <FormFeedback>{errors.telnum}</FormFeedback>                             
                        </Col>
                    </FormGroup>

                    <br></br>
                    <FormGroup row>
                        <Label htmlFor='email' md={2}>Email</Label>
                        <Col md={10}>
                            <Input type="email" id="email" name="email" placeholder="Email"
                             value={this.state.email}
                             valid = {errors.email === ''}
                             invalid = {errors.email != ''}
                             onBlur = {this.handleBlur('email')}
                             onChange = {this.handleInputChange}  />
                            <FormFeedback>{errors.email}</FormFeedback>                             
                        </Col>
                    </FormGroup>

                    <br></br>
                    <FormGroup row>
                        <Col md={{size: 6 , offset:2}}>
                            <FormGroup check>
                                <Label check>
                                    <Input type="checkbox" name="agree"
                                    checked={this.state.agree}
                                    onChange = {this.handleInputChange} /> {' '}
                                    <strong>May we contact you</strong>
                                </Label>
                            </FormGroup>
                        </Col>
                        <Col md={{size: 3, offset: 1}}>
                            <Input type="select" id="contactType" name="contactType"
                             value={this.state.contactType}
                             onChange = {this.handleInputChange}  >
                                <option>Tel.</option>
                                <option>Email</option>
                             </Input>
                        </Col>

                    <br></br>
                        <FormGroup row>
                        <Label htmlFor='message' md={2}>Your Feedback</Label>
                        <Col md={5}>
                            <Input type="textarea" id="message" name="message" rows="10"
                             value={this.state.message}
                             onChange = {this.handleInputChange}  />
                    <br></br> 
                        </Col>
                        </FormGroup>
                        <FormGroup row>
                            <Col md={{size:10,offset:2}}>
                                <Button type="submit" color="primary">
                                    Send Feedback
                                </Button>
                            </Col>
                        </FormGroup>
                    </FormGroup>
                </Form>

            </div>
        </div>
    </div>
);
}

}

export default Contact;