import { Component } from 'react';
import Menu from './MenuComponents';
import Home from './HomeComponent';
import Header from './HeaderComponent';
import Footer from './FooterComponent';
import ShoeDetail from './ShoedetailComponent';
import Contact from './ContactComponent';
import DISHES from '../shared/dishes';
import COMMENTS from '../shared/comments';
import PROMOTIONS from '../shared/promotions';
import LEADERS from '../shared/leaders';

import {Switch , Redirect , Route, HashRouter as Router} from 'react-router-dom';

class Main extends Component {
  constructor(props){
    super(props);
    this.state = {
      shoes : DISHES ,
      comments : COMMENTS,
      promotions : PROMOTIONS,
      leaders : LEADERS
      // selectedDish : null
    };
  }
//   onDishSelect(dishId){
//     this.setState({selectedDish:dishId})
// }
  render(){
    const Homepage = () =>{
      return(
        <Home shoe = {this.state.shoes.filter((shoe)=> shoe.featured)[0]}
        promo = {this.state.promotions.filter((promo)=> promo.featured)[0]}
        leader = {this.state.promotions.filter((leader)=> leader.featured)[0]}/>
      );
    }
    const DishWithId = ({match}) =>{
      return(
        <ShoeDetail dish={this.state.shoes.filter((dish)=> dish.id === parseInt(match.params.dishId,10))[0]}
        />
      );
    }
    //   const ContactUs = () =>{
    //     return(
    //       <Contact />
    //     );
    //   }
    
  return (
    <div>
      <Router>
      <Header />
      
      <Switch>
        <Route path="/home" component={Homepage} />
        <Route exact path="/featureditems" component = {() => <Menu shoe ={this.state.shoes}/>}/>
        <Route exact path="/contactus" component={Contact} />
        <Route path="/featureditems/:dishId" component={DishWithId}/>
        <Redirect to="/home"/>
      </Switch>
      
      {/* < Menu dishes={this.state.dishes} 
      onClick = {(dishId)=>this.onDishSelect(dishId)}/>
      <ShoeDetail dish={this.state.dishes.filter((dish)=> dish.id === this.state.selectedDish)[0]} /> */}
    <Footer />
    </Router>
    </div>
  );
}
}

export default Main;
